.footer-container {
    background-color: #191b1f ;
  
    position: relative;
    height: 400px;
   
}
.map-container {
    position: absolute;
    border-radius: 20px;
    top: 10% ; 
    right : 5% ; 
    width : 40% ; 
    height: 300px;

    float: right;
    background-color: #191b1f;
    box-sizing: border-box ;
    
}

.footer-container img{
    margin-right: 3%;
    margin-left: 5%;
    position: absolute;
    top: 50% ; 
    transform: translate(0 , -50%);
    height: 100px;
}

.footer-container p { 
   
  
    align-items: center;
    max-width: 300px;
    color: white;
    
}

.iframe{
    width : 100% ;
    height: 300px;
    border-radius: 20px;
    margin:0;

}

.social { 
    
        display: grid ; 
        height: 20px;
        margin-left: auto; 
        margin-right: auto;
        grid-template-columns: auto auto auto ;
        padding: 10px;
        /* background-image: linear-gradient(to bottom right, rgb(113, 223, 217), rgb(148, 6, 94)); */

}
.social-item{
    width :20px;
    height: 20px;
}

@media only screen and (max-width: 400px) {
    
    .footer-container{
        height:600px;
        display: block;
    }
    .footer-container img{
       
        width:300 ; 
        height: 100px; 
        position: absolute ;
        left : 45%; 
        top: 70% ;
        transform: translate(-50% , 0);
        height: 100px;
    }
    .map-container {
        position: absolute;
        transform: translate(-50% , 0);
        left:50%;
        width : 100% ; 
        height: 300px;
    
        float: right;
        background-color: aquamarine;
        
    }
}


@media only screen and (max-width: 900px) {
    
    .footer-container{
        height:600px;
        display: block;
    }
    .footer-container img{
       
        width:300 ; 
        height: 100px; 
        position: absolute ;
        left : 45%; 
        top: 70% ;
        transform: translate(-50% , 0);
        height: 100px;
    }
    .map-container {
        position: absolute;
        transform: translate(-50% , 0);
        left:50%;
        width : 300px ; 
        height: 300px;
    
        float: right;
        background-color: aquamarine;
        
    }
}

@media only screen and (min-width: 1200px) {
    
    .footer-container img {

        margin-left: 15%;
        position: absolute;
        top: 50% ; 
        transform: translate(0 , -50%);
        height: 100px;
    }


}

